/* oswald-regular - latin_cyrillic */
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("Oswald"),
    url("../fonts/oswald-v35-latin_cyrillic-regular.woff2") format("woff2"),
    url("../fonts/oswald-v35-latin_cyrillic-regular.woff") format("woff");
}

@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: local("Oswald"),
    url("../fonts/oswald-v35-latin_cyrillic-500.woff2") format("woff2"),
    url("../fonts/oswald-v35-latin_cyrillic-500.woff") format("woff");
}
